<template>
    <div>
        <div v-if="show" style="position:fixed;top:0;left:0;width:100vw;height:100vh;background:black;opacity:0.3;">

        </div>
        <div v-if="show" style="position: fixed;top: 50%;left: 50%;transform: translateX(-50%) translateY(-50%);z-index:10;">
            <div style="width:100%;" class="bg-white p-3 shadow-md rounded-lg p-3">
                <p class="text-xl p-3">
                    <!-- {{pilih.translation_id}} -->
                </p>
            </div>
        </div>
        <div class="container" >
            <div class="row justify-content-center sm:p-4">
                <div class="col-12">
                    <br />
                    <p class="p-3 my-2 text-center text-2xl font-bold font-italic bg-white  mt-3 rounded-lg shadow">Ayat Kursi</p>
                    <div class="row">
                        <div class="col-sm-6 ">
                        <!-- <div class="input-group rounded-lg shadow mb-2 bg-white p-4">
                            <div v-if="listPencarian.length>0">
                                <p class="font-semibold p-2">Index Pencarian : </p>
                                <v-select :options="listPencarian" style="min-width:300px;" label="title" v-model="cari" :reduce="e => e.value"></v-select>
                            </div>
                        </div> -->
                        </div>
                        <div class=" col-sm-12 pl-3 pr-3">
                        <div class="bg-white  rounded-lg shadow p-4">
                            <p class="font-semibold p-2">Ukuran huruf : </p>
                            <div class="sm-form ">
                            <select
                                class="form-control text-sm"
                                style="height: 34px; width: 200px"
                                v-model="pilihanLebar"
                            >
                                <option
                                v-for="(item, index) in lebar"
                                :key="index"
                                :value="item"
                                >
                                {{ index + 1 }}
                                </option>
                            </select>
                            </div>
                        </div>
                        </div>
                    </div>
                    </div>
                <div class="col-12 col-sm-12 cursor-pointer" @click="pilih=item;show=!show;" v-for="(item, index) in td" :key="index+'asmaulhusna'" v-show="index<1">
                    <div class="rounded-lg shadow bg-white  p-2 sm:p-5 mt-3">
                        <p class="pb-3 text-3xl font-italic">
                            {{item.nama}}
                        </p>
                        <p class="pb-3 text-center "  :class="`${pilihanLebar}`" style="font-family: 'Amiri', serif;line-height: 2.0;">
                            {{item.arab}}
                        </p>
                        <p class="pb-3 text-lg font-italic">
                            {{item.latin}}
                        </p>
                        <p class="pt-2 text-sm">
                            {{item.arti}}
                        </p>
                        <!-- <span>
                            {{item.index}}
                        </span>
                        <p class="text-center text-4xl" style="font-family: 'Amiri', serif;line-height: 2.0;">
                            {{item.arabic}}
                        </p>
                        <p class="text-sm text-center mt-2">
                            {{item.latin}}
                        </p> -->
                        <p></p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default{
    computed:{
        td(){
            let that=this;
            let data =this.datanya;
            data=data.filter(e=>{
                if(e.nama.toString().toLowerCase().indexOf(that.cari)!=-1){
                    return e;
                }
            })
            return data;
        }
    },
    layout:'app_islamic',
    data(){
        return{
            cari:'',
             lebar:[
                'text-lg',
                'text-xl',
                'text-2xl',
                'text-4xl',
                'text-5xl',
            ],
            pilihanLebar:'text-2xl',
            datanya:[
                {
                    "nama": "Do'a Salamat dunia dan akhirat",
                    "arab": "اَللّٰهُمَّ اِنَّا نَسْئَلُكَ سَلَامَةً فِى الدِّيْنِ، وَعَافِيَةً فِى الْجَسَدِ وَزِيَادَةً فِى الْعِلْمِ وَبَرَكَةً فِى الرِّزْقِ وَتَوْبَةَ قَبْلَ الْمَوْتِ وَرَحْمَةً عِنْدَ الْمَوْتِ وَمَغْفِرَةً بَعْدَ الْمَوْتِ، اَللّٰهُمَّ هَوِّنْ عَلَيْنَا فِيْ سَكَرَاتِ الْمَوْتِ، وَنَجَاةً مِنَ النَّارِ وَالْعَفْوَعِنْدَ الْحِسَابِ، رَبَّنَا لاَتُزِغْ قُلُوْبَنَا بَعْدَ اِذْهَدَيْتَنَا وَهَبْ لَنَا مِنْ لَدُنْكَ رَحْمَةً اِنَّكَ اَنْتَ الْوَهَّابُ، رَبَّنَا أَتِنَا فِى الدُّنْيَا حَسَنَةً وَفِي اْلأَخِرَةِ حَسَنَةً وَقِنَا عَذَابَ النَّارِ",
                    "latin": "Allahumma innaa nas-aluka salaamatan fid diini wa 'afiyatan fil jasadi wa ziyaadatan fil 'ilmi wa barokatan fir rizqi wa taubatan qablal mauti wa rohmatan 'indal mauti wa maghfirotan ba'dal mauti, allahummaa hawwin 'alainaa fil sakaraatil mauti wannajaata minan naari wal 'afwa 'indal hisaabi, rabbanaa laa tuzigh quluubana ba'da idz hadaitanaa wahab lanaa mil ladunka rahma, innaka antal wahhaab, rabbanaa aatinaa fiddunnyaa hasanah, wa fil akhirati hasanah waqinaa 'adzaa ban naar.",
                    "arti": "“Ya Allah kami memohon kepadaMu keselamatan dalam agama, dan kesejahteraan/kesegaran pada tubuh dan penambahan ilmu, dan keberkahan rizqi, serta taubat sebelum mati dan rahmar di waktu mati, dan keampunan sesudah mati. Ya, Allah, mudahkanlah kami saat pencabutan nyawa selamat dari api neraka dan mendapat kemaafan ketika amal diperhitungkan. Ya Allah, janganlah Engkau goyahkan hati kami setelah Engkau beri petunjuk dan berilah kami rahmat dari sisi-Mu. Sesungguhnya Engkau Maha Pemberi. Ya Tuhan kami, berilah kami kebaikan hidup di dunia dan kebaikan hidup di akhirat, dan jagalah kami dari siksa api neraka."
                },            
            ],
            show:false,
            pilih:{}
        }
    },
    mounted() {
        let that=this;
        function onBackKeyDown(e){
          e.preventDefault();
          e.stopPropagation();
          that.$router.push('/app/islamic')
        };
        document.addEventListener("backbutton", onBackKeyDown, false);
    },
      metaInfo: {
        title: `Baca Al Qur'an`,
        link: [
         { rel: 'preconnect', href: 'https://fonts.googleapis.com' },
         { rel: 'preconnect', href: 'https://fonts.gstatic.com' },
         { rel: 'stylesheet', href: 'https://fonts.googleapis.com/css2?family=Amiri&display=swap' },
        ],
    },
}
</script>